/*Calendar css*/
.calendar-area {
  position: relative;
  margin-top: 60px;
}

.calendar-area #calendar {
  background: #fff;
  border-radius: 0;
  border: 1px solid #ddd;
}

.calendar-area .current-date {
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid #ddd;
  padding: 15px 10px;
}

.calendar-area .current-month {
  color: #0D152E;
  font-size: 13px;
  font-weight: 700;
  font-style: normal;
  letter-spacing: -0.09px;
  line-height: normal;
}

.calendar-area .week-days {
  display: flex;
  justify-content: space-between;
  color: #dfebed;
  font-weight: 600;
  padding: 0px 0px;
}

.calendar-area .week-day {
  padding: 10px;
  text-align: center;
  width: 43px;
  color: #161c2d;
  font-size: 13px;
  font-weight: 700;
  font-style: normal;
  letter-spacing: -0.09px;
  line-height: normal;
  text-transform: uppercase;
}

.calendar-area .days {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.calendar-area .weeks {
  display: flex;
  flex-direction: column;
  color: #fff;
  padding: 0 0px;
}

.calendar-area .week {
  display: flex;
  justify-content: space-between;
  font-size: 1.2rem;
  margin-bottom: 0px;
}

.calendar-area .not-current-month {
  opacity: 0.3;
}

.calendar-area .day {
  padding: 10px;
  text-align: center;
  width: 43px;
  color: #81838C;
  font-size: 13px;
  font-weight: 700;
  font-style: normal;
  letter-spacing: -0.09px;
  line-height: normal;
}

.calendar-area .day.today {
  width: 41px;
  height: 40px;
  background-color: #416ff4;
  color: #fff;
}

.calendar-area .day:not(.not-current-month):hover {
  font-weight: 600;
}

.calendar-area #prev,
.calendar-area #next {
  position: absolute;
  background: transparent;
  border: none;
  cursor: pointer;
  color: #81838C;
  padding: 15px;
  font-size: 13px;
  font-weight: 700;
  font-style: normal;
  letter-spacing: -0.09px;
  line-height: normal;
  text-align: left;
}

.calendar-area #prev:before {
  content: "<";
  padding-right: 4px;
}

.calendar-area #next {
  right: 0;
}

.calendar-area #next:after {
  content: ">";
  padding-left: 4px;
}

.calendar-area #week-starter {
  position: fixed;
  top: 0;
  right: 0;
  color: #2b4450;
  padding: 20px;
  text-shadow: 2px 2px 1px #dfebed;
}

.calendar-area .week-starter__choice {
  cursor: pointer;
}

.calendar-area .week-starter__choice > input {
  cursor: pointer;
}

