/*>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>      
      Hero Area 
<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<*/


.bg-position {
    background-repeat: no-repeat !important;
    background-size: cover !important;

}


.hero--area-curve__2 {
    position: relative;
    z-index: 1;
    overflow: hidden;

    @include desktops {
        &:after {
            position: absolute;
            content: "";
            background: $catskillwhite;
            width: 105%;
            height: 420px;
            left: -1%;
            bottom: -23%;
            z-index: -1;
            transform: rotate(-9deg);

            @include large-desktops {
                transform: rotate(-10deg);
            }
        }
    }


}


/*>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>      
      Landing 1 Hero Area
<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<*/


.hero-area {
    background-position: 100% 100% !important;

    padding: 100px 0px 0px 0px;

    @include mobile {
        padding: 120px 0px 0px 0px;
    }

    @include mobile-lg {
        padding: 125px 0px 0px 0px;
    }

    @include tablet {
        padding: 145px 0px 0px 0px;
    }

    @include desktops {
        padding: 155px 0px 0px 0px;
    }

    .hero-area__title {
        h2 {

            font-weight: 700;
            font-style: normal;
            letter-spacing: -1.81px;

            @include mobile-xs {
                font-size: 36px;
                line-height: 41px;
            }

            @include mobile-lg {
                font-size: 45px;
                line-height: 50px;
            }

            @include tablet {
                font-size: 55px;
                line-height: 60px;
            }
        }
    }

    .badge {
        h6 {
            font-size: 14px;
            font-weight: 400;
            font-style: normal;
            letter-spacing: -0.46px;
            line-height: normal;
            padding: 5px;
            border-radius: 30px;
        }

        &-offer {
            padding: 0px 15px !important;
            border-radius: 30px;
        }

        & span {
            padding: 0px 12px 0px 15px;
        }
    }

    .hero-area__btn {

        @include mobile-xs {
            margin-top: 40px;
        }

        @include tablet {
            margin-top: 55px;
        }

        .btn {
            font-size: 16px;
            font-weight: 700;
            font-style: normal;
            letter-spacing: -0.53px;
            line-height: normal;
            text-align: center;
            border-radius: 6px;
            padding: 18px 28px;
        }
    }

    .hero-area__image {

        @include mobile-xs {
            margin-top: 50px;
        }


        @include desktops {
            margin-top: 110px;
        }

        & img {
            width: 100%;
        }
    }

}


/*>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>      
      Landing 2 Hero Area
<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<*/


.hero-area-2 {

    @include mobile-xs {
        padding-top: 110px;
        padding-bottom: 30px;
    }

    @include mobile {
        padding-top: 160px;
        padding-bottom: 60px;
    }

    @include tablet {
        padding-top: 180px;
        padding-bottom: 80px;
    }

    @include desktops {
        padding-top: 250px;
        padding-bottom: 175px;
    }

    .hero-area-content {
        padding-top: 35px;

        @include tablet {
            margin-right: 40px;

        }

        @include large-desktops {
            margin-right: 85px;
            padding-top: 0px;
        }

        @include large-desktops-mid {
            margin-right: 35px;
        }

        @include extra-large-desktops {
            margin-right: 160px;
        }

        h2 {
            color: $haiti;
            font-size: 36px;
            font-weight: 700;
            font-style: normal;
            letter-spacing: -2.11px;
            line-height: 47px;

            @include mobile-lg {
                font-size: 48px;
                line-height: 60px;
            }

            @include tablet {
                font-size: 64px;
                line-height: 70px;
            }

            @include desktops {
                font-size: 62px;
            }

            @include large-desktops {
                font-size: 64px;
            }
        }

        p {
            color: #81838c;
            font-size: 20px;
            font-weight: 400;
            font-style: normal;
            letter-spacing: -0.66px;
            line-height: 34px;
            margin-top: 30px;
        }

        .hero-area-btn-group {
            margin: 48px -8px -20px -8px;

            a {
                margin: 0px 8px 20px 8px;
                display: inline-block;
            }
        }
    }

    .hero-area-image-group {
        position: relative;

        .image-1 {
            img {
                box-shadow: 31px 40px 78px rgba(111, 118, 138, 0.16);
                border-radius: 30px;
            }

        }

        .image-2 {
            position: absolute;
            top: -5%;
            left: 0%;
            z-index: -1;

            @include mobile {
                left: 10%;
            }

            @include mobile-lg {
                left: 12%;
            }

            @include tablet {
                left: 15%;
            }

            @include desktops {
                left: 11%;
            }

            @include large-desktops {
                left: 17%;
            }

            @include extra-large-desktops {
                left: 22%;
            }

            img {
                @include mobile-xs {
                    display: none;
                }

                @include mobile {
                    display: block;
                }
            }
        }

        .image-3 {
            position: absolute;

            @include mobile-xs {
                top: 64%;
                left: 50%;
                transform: translate(-50%, -50%);
            }

            @include mobile {
                top: 18%;
                right: 0;
                left: inherit;
                transform: inherit
            }

            img {
                box-shadow: 24px 26px 57px rgba(111, 118, 138, 0.16);
                border-radius: 30px;

            }
        }

        .image-4 {
            position: absolute;
            top: 53%;
            left: 2%;

            @include mobile-lg {
                left: -7%;
            }

            @include tablet {
                left: 2%;
            }

            @include desktops {
                left: 0%;
            }

            @include large-desktops {
                left: 7%;
            }

            @include large-desktops-mid {
                left: -4%;
            }

            @include extra-large-desktops {
                left: 9%;
            }

            img {
                box-shadow: 8px 39px 36px rgba(111, 118, 138, 0.16);
                border-radius: 30px;

                @include mobile-xs {
                    display: none;
                }

                @include mobile {
                    display: block;
                    transform: scale(.8);
                }

                @include mobile-lg {
                    transform: scale(1);
                }

                @include desktops {
                    transform: scale(.8);
                }

                @include large-desktops {
                    transform: scale(1);
                }
            }
        }

        .image-5 {
            position: absolute;
            top: 52%;
            right: 0%;

            @include large-desktops {
                right: 0%;
            }

            @include large-desktops-mid {
                right: 9%;
            }

            img {
                box-shadow: -28px 39px 60px rgba(111, 118, 138, 0.16);
                border-radius: 30px;

                @include mobile-xs {
                    display: none;
                }

                @include mobile {
                    display: block;
                    transform: scale(.8);
                }

                @include mobile-lg {
                    transform: scale(1);
                }

                @include desktops {
                    transform: scale(.8);
                }

                @include large-desktops {
                    transform: scale(1);
                }

            }
        }
    }
}

/*>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>      
      Landing 3 Hero Area
<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<*/

.hero-area-l3 {
    position: relative;
    z-index: 1;
    padding: 87px 0px 60px 0px;

    @include mobile {
        padding: 107px 0px 60px 0px;
    }

    @include mobile-lg {
        padding: 92px 0px 60px 0px;
    }

    @include tablet {
        padding: 140px 0px 80px 0px;
    }

    @include desktops {
        padding: 200px 0px 130px 0px;
    }

    &:after {
        position: absolute;
        content: '';
        background: $bunting;
        height: 100%;
        width: 100%;
        top: 0;
        left: 0;
        opacity: .8;
        z-index: -1;

    }

    &__content {

        margin-top: 45px;

        @include tablet {
            padding-right: 80px;

        }

        @include desktops {
            margin-top: 0;
            padding-right: 0px;
        }

        @include large-desktops {
            padding-right: 45px;
        }

        @include extra-large-desktops {
            padding-right: 134px;
        }

        h2 {
            color: $white;
            font-size: 36px;
            font-weight: 700;
            font-style: normal;
            letter-spacing: -1.58px;
            line-height: 50px;

            @include mobile {
                font-size: 40px;
            }

            @include mobile-lg {
                font-size: 48px;
                line-height: 60px;
            }
        }

        p {
            color: $lynch;
            font-size: 20px;
            font-weight: 400;
            font-style: normal;
            letter-spacing: -0.66px;
            line-height: 34px;
            margin-top: 30px;
        }

        .btn {
            width: 192px;
            height: 60px;
            border-radius: 7px;
            background-color: $primary;
            color: $white;
            font-size: 16px;
            font-weight: 700;
            font-style: normal;
            letter-spacing: -0.53px;
            line-height: normal;
            margin-top: 32px;
        }
    }
}

.hero-area-l3-image-group {
    position: relative;

    .image-1 {
        & img {

            @include mobile-lg {
                transform: scale(.9);
            }

            @include tablet {
                transform: scale(1);
            }
        }
    }

    .image-2 {
        position: absolute;
        left: -53px;
        top: -65px;

        @include mobile {
            left: -14px;
            top: -22px;
        }

        @include mobile-lg {
            left: 27px;
            top: 32px;
        }

        & img {
            transform: scale(.5);

            @include mobile {
                transform: scale(.7);
            }

            @include mobile-lg {
                transform: scale(.8);
            }

            @include tablet {
                transform: scale(1);
            }
        }

    }

    .image-3 {
        position: absolute;
        right: -38px;
        bottom: -45px;

        @include mobile {
            right: -4px;
            bottom: 45px;
        }

        @include mobile-lg {
            right: 20px;
            bottom: 30px;
        }

        @include desktops {
            right: -20px;
            bottom: -50px;
        }

        @include large-desktops {
            bottom: -15px;
            right: -15px;
        }


        @include extra-large-desktops {
            bottom: 50px;
            right: 15px;
        }

        & img {
            transform: scale(.5);

            @include mobile {
                transform: scale(.7);
            }

            @include mobile-lg {
                transform: scale(.8);
            }

            @include tablet {
                transform: scale(1);
            }
        }

    }
}

