        position: absolute;
        top: 14%;
        left: 0;
    }

    .hero-area-content {
        padding-top: 0px;
        padding-bottom: 60px;

        @include tablet {
            padding-top: 20px;
            padding-bottom: 80px;
        }

        @include desktops {
            padding-top: 323px;
            padding-bottom: 380px;
        }

        h2 {

