            font-style: normal;
            letter-spacing: -1.98px;
            font-size: 37px;
            line-height: 47px;

            @include mobile {
                font-size: 50px;
                line-height: 60px;
            }

            @include mobile-lg {
                font-size: 60px;
                line-height: 72px;
            }

            @include desktops {
                font-size: 55px;
                line-height: 68px;
            }

            @include large-desktops {
                font-size: 60px;
                line-height: 72px;
            }
        }

        p {
            font-size: 20px;
            font-weight: 400;
            font-style: normal;
            letter-spacing: -0.66px;
            line-height: 34px;
            padding-right: 0px;
            margin-top: 35px;

            @include tablet {
                padding-right: 75px;
            }

            @include large-desktops {
                padding-right: 45px;
            }

            @include large-desktops-mid {
                padding-right: 135px;
            }
        }

        .hero-area__btn {
            .btn {
                min-width: 100%;
                height: 60px;
                border-radius: 5px;
                margin-top: 25px;

                @include mobile-lg {
                    min-width: 180px;
                }
            }


        }
    }

    .hero-area-image-group {
        position: relative;
        padding-top: 75px;

        @include mobile {
            padding-top: 75px;
        }

        @include tablet {
            padding-top: 85px;
        }

        @include desktops {
            padding-top: 0;
        }

        .image-1 {
            transform: rotate(15deg);

            @include desktops {
                transform: rotate(0deg);
            }

            @include large-desktops {
                position: absolute;
                top: 45px;
                left: 35%;

            }

            @include large-desktops-mid {
                left: 50%;
            }

            & img {
                width: 100%;

                @include desktops {
                    width: initial;
                }
            }

        }

        .image-2 {
            display: none;

            @include tablet {
                display: block;
                position: absolute;
                top: 40%;
                left: -12%;
                transform: rotate(15deg);
            }

            @include desktops {
                left: 15%;
                top: 120px;
                transform: rotate(0deg);
            }

            @include large-desktops {
                top: 315px;
                left: 6%;
            }

            @include large-desktops-mid {
                top: 300px;
                left: 23%;
            }

        }

        .image-3 {

            display: none;

            @include tablet {
                display: block;
                position: absolute;
                top: 30%;
                right: -20%;
                left: inherit;
                transform: rotate(15deg);
            }

            @include desktops {
                top: 500px;
                transform: rotate(0deg);
                right: 36%;
            }

            @include large-desktops {
                top: 600px;
                left: -8%;
            }

            @include large-desktops-mid {
                top: 588px;
                left: 7%;
            }
            @include extra-large-desktops {
                top: 594px;
                left: 11%;
            }
          

        }
    }
}

