            }

            @include mobile-lg {
                font-size: 48px;
                line-height: 60px;
            }
        }

        p {
            color: $waterloo;
            font-size: 20px;
            font-weight: 400;
            font-style: normal;
            letter-spacing: -0.66px;
            line-height: 34px;
            margin-top: 40px;
        }

        &__btn {
            .btn {
                min-width: 180px;
                height: 60px;
                box-shadow: 21px 15px 23px rgba(65, 111, 244, 0.11);
                border-radius: 33px;
                color: $white;
                font-size: 16px;
                font-weight: 700;
                font-style: normal;
                letter-spacing: -0.53px;
                line-height: normal;
                margin-top: 25px;
            }
        }
    }
}

/*>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>      
      Landing 9 Hero Area
<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<*/

.hero-area-l9 {
    padding-top: 108px;

    @include mobile-lg {
        padding-top: 113px;
    }

    @include tablet {
        padding-top: 125px;
    }

    @include desktops {
        padding-top: 0px;
    }
}


.hero-image {

    position: relative;

    @include desktops {
        &:after {
            position: absolute;
            content: "";
            background: #0D152E;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            opacity: 0.5;
            z-index: 0;
        }
    }


    @include desktops {
        position: absolute;
        top: 0;
        left: 0;
        background-size: cover;
        width: 45%;
        height: 100%;
        background-position: center;

        & img {
            display: none;
            ;
        }
    }

    @include large-desktops {
        width: 42%;
    }

    @include extra-large-desktops {
        width: 38%;
        background-position: 100%;
    }


    .video-icon {

        height: 96px;
        width: 96px;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        display: flex;
        align-items: center;
        justify-content: center;
        z-index: 1;


        @include desktops {
            left: initial;
            right: 0;
            top: 50%;
            transform: translate(50%, -50%);
        }


        i {
            width: 66px;
            height: 66px;
            color: $primary;
            box-shadow: 0 14px 25px rgba(132, 132, 132, 0.16);
            background-color: $white;
            border-radius: 50%;
            text-align: center;
            line-height: 66px;
            position: relative;

        }

        &:after {
            position: absolute;
            content: "";
            background: $primary;
            width: 100%;
            height: 100%;
            left: 0;
            top: 0;
            opacity: .3;
            border-radius: 50%;
            z-index: -1;
        }
    }


}


.content {
    &--l9-1 {
        position: relative;
        padding: 30px 0px 60px 0px;

        @include tablet {
            padding: 50px 0px 80px 0px;
