/*>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>      
     Feature Area L-1
<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<*/

.feature-area {
    @include mobile-xs {
        padding-top: 45px;
    }

    @include tablet {
        padding-top: 65px;
    }

    @include desktops {
        padding-top: 115px;
    }

    & .feature--card {
        border: 0;

        @include mobile-xs {
            padding-top: 40px;
        }

        @include tablet {
            padding-top: 60px;
        }

        @include desktops {
            padding-top: 85px;
        }
    }

    & .card__icon {
        margin: auto;
        width: 70px;
        height: 70px;
        box-shadow: 0 22px 24px rgba(65, 111, 244, 0.2);
        border-radius: 9px;
        background-color: $primary;

        &__2 {
            box-shadow: 0 22px 24px rgba(43, 214, 123, 0.2);
            background-color: #2bd67b;
        }

        &__3 {
            box-shadow: 0 22px 24px rgba(247, 227, 109, 0.2);
            background-color: #f7e36d;
        }

        & i {
            color: $white;
            line-height: 70px;
            font-size: 25px;
        }

    }

    & .card-body {
        & .card__title {
            margin-bottom: 18px;
        }

        & h4 {
            margin-top: 20px;
            color: $haiti;
            font-size: 20px;
            font-weight: 700;
            font-style: normal;
            letter-spacing: -0.66px;
            line-height: normal;
        }

        & p {
            color: #81838c;
            font-size: 16px;
            font-weight: 400;
            font-style: normal;
            letter-spacing: -0.5px;
            line-height: 28px;
        }


    }

}

.feature-l2-try-btn {
    .btn {
        min-width: 180px;
        height: 60px;
        border-radius: 6px;
        background-color: $primary;
        color: #f4f7fa;
        font-size: 13px;
        font-weight: 700;
        font-style: normal;
        letter-spacing: 1.63px;
        line-height: normal;
        text-align: center;
        text-transform: uppercase;
        margin-top: 55px;
    }
}

.feature-area-2 {
    @include mobile-xs {
        padding-top: 0px;
    }

    @include desktops {
        padding-top: 130px;
    }

    @include large-desktops {
        padding-top: 170px;
    }

    .section__heading {
        p {
            @include large-desktops {
                margin-right: 0px;
            }

            @include large-desktops-mid {
                margin-right: 0px;
            }

            @include extra-large-desktops {
                margin-right: 70px;
            }
        }
    }

    .feature-area-2-content {
        @include mobile-xs {
            padding-top: 30px;
        }

        @include tablet {
            padding-top: 40px;
        }

        @include desktops {
            padding-top: 0px;
        }

        .feature--item {
            margin-top: 50px;
            margin-bottom: -30px;

            .col-lg-7{
                margin-bottom: 30px;
            }
        }

        .widget {
            .icon__box {
                text-align: center;
                width: 60px;
                height: 60px;
                line-height: 60px;
                box-shadow: 8px 11px 26px rgba(145, 145, 145, 0.16);
                border-radius: 10px;


                svg {
                    width: 16px;
                }
            }

            .widget-body {
                margin-left: 27px;

                h6 {
                    color: $haiti;
                    font-weight: 700;
                    font-style: normal;
                    letter-spacing: -0.66px;
                    font-size: 20px;
                    line-height: 30px;
                }
            }

        }
    }

    &-img-group {
        position: relative;

        .image__1 {
            @include desktops {
                margin-left: 30px;
            }

            @include large-desktops {
                margin-left: 70px;
            }

            & img {
                box-shadow: -59px 3px 99px rgba(129, 129, 129, 0.16);
                border-radius: 10px;

                @include mobile-xs {
                    width: 100%;
                }

                @include desktops {
                    width: initial;
                }
            }
        }



    }
}


/*>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>      
     Feature Area L-2
<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<*/

.feature-area-3 {
    padding-top: 30px;
    padding-bottom: 35px;

    @include tablet {
        padding-bottom: 55px;
    }

    @include desktops {
        padding-bottom: 105px;
    }

    .feature-area-3-image-group {
        position: relative;

        .image-1 {
            img {
                box-shadow: 32px 51px 84px rgba(111, 118, 138, 0.16);
                border-radius: 30px;
            }
        }

        .image-2 {
            position: absolute;
            top: -5%;
            left: 0%;
            z-index: -1;

            @include mobile-xs {
                display: none;
            }

            @include mobile {
                display: block;
                left: 14%;
            }

            @include mobile-lg {
                left: 18%;
            }

            @include desktops {
                left: 13%;
            }

            @include large-desktops {
                left: 20%;
            }


            @include extra-large-desktops {
                left: 24%;
            }

        }

        .image-3 {
            position: absolute;
            top: 50%;
            left: 0%;

            @include mobile {
                left: 8%;
            }

            @include mobile-lg {
                left: 13%;
            }

            @include large-desktops {
                left: 7%;
            }

            @include large-desktops-mid {
                left: 4%;
            }

            @include extra-large-desktops {
                left: 13%;
            }

            img {
                box-shadow: -15px 24px 78px rgba(111, 118, 138, 0.16);
                border-radius: 5px;

                @include mobile-xs {
                    display: none;
                }

                @include mobile {
                    display: block;
                    transform: scale(.8);
                }

                @include mobile-lg {
                    transform: scale(1);
                }

                @include desktops {
                    transform: scale(.8);
                }

                @include large-desktops {
                    transform: scale(1);
                }
            }
        }

        .image-4 {
            position: absolute;

            @include mobile-xs {
                position: absolute;
                top: 60%;
                left: 50%;
                transform: translate(-50%, -50%);
            }

            @include mobile {
                top: 50%;
                right: 10%;
                left: inherit;
                transform: inherit
            }

            @include desktops {
                right: 4%;
            }

            @include large-desktops {
                right: 18%;
            }

            @include large-desktops-mid {
                right: 22%;
            }

            img {
                box-shadow: -15px 24px 78px rgba(111, 118, 138, 0.16);
                border-radius: 10px;

                @include mobile {
                    transform: scale(.8);
                }

                @include mobile-lg {
                    transform: scale(1);
                }

                @include desktops {
                    transform: scale(.8);
                }

                @include large-desktops {
                    transform: scale(1);
                }
            }
        }
    }

    .feature-area-3-content {
        padding-top: 35px;

        @include large-desktops {
            padding-top: 0px;
            margin-right: 75px;
        }

        @include extra-large-desktops {
            margin-right: 165px;
        }

        h2 {
            color: $haiti;
            font-size: 36px;
            font-weight: 700;
            font-style: normal;
            letter-spacing: -1.58px;
            line-height: 47px;

            @include mobile-lg {
                font-size: 48px;
                line-height: 60px;
            }
        }

        p {
            color: #81838C;
            font-size: 20px;
            font-weight: 400;
            font-style: normal;
            letter-spacing: -0.66px;
            line-height: 34px;
            margin-top: 25px
        }

        .feature-area-3-service {
            margin-top: 53px;

            ul {
                margin-bottom: -23px;

                li {
                    margin-bottom: 23px;
                }
            }

            .service-icon {
                border-radius: 10px;
                margin-right: 32px;

                i {
                    height: 60px;
                    width: 60px;
                    text-align: center;
                    font-size: 25px;
                    line-height: 60px;
                    transform: rotate(-45deg);
                }
            }

            .service-content {
                margin-top: -6px;

                h4 {
                    color: $haiti;
                    font-size: 20px;
                    font-weight: 700;
                    font-style: normal;
                    letter-spacing: -0.66px;
                    line-height: normal;
                }

                p {
                    color: $waterloo;
                    font-size: 16px;
                    font-weight: 400;
                    font-style: normal;
                    letter-spacing: -0.53px;
                    line-height: 28px;
                    margin-top: 20px;
                }
            }
        }
    }
}


